body {
    overflow-x: hidden;
    text-align: center;
    font-family: ubuntu, "Yu Mincho", 游明朝, source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    /* background: radial-gradient(rgb(165,243,203) 40px, rgb(86,173,126)); */
    background: url("/images/photowall.jpg") no-repeat scroll 50% 0 / cover;
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    position: fixed;
    left: 0;
    top: 0;
    overflow-y: hidden;
}

@media screen and (orientation: portrait) {

    /* vertical */
    body {
        padding: 0 0;
        width: 100vw;
    }

    div.navi {
        margin: 0;
    }

    div.card {
        width: 64vw;
        font-size: 95%;
        padding: 6vh 6vw;
    }

    div.card.nopad {
        width: 76vw;
        padding: 0;
    }

}

@media screen and (orientation: landscape) {

    /* horizontal */
    body {
        padding: 0 2vw;
        width: 96vw;
    }

    div.navi {
        margin: 0 -2vw;
    }

    div.card {
        width: 40vw;
        font-size: 110%;
        padding: 6vh 8vw;
    }

    div.card.nopad {
        width: 56vw;
        padding: 0;
    }

}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    appearance: none;
}

input[type="number"] {
    -moz-appearance: textfield;
}

input:focus {
    outline: none;
}

button:focus {
    outline: none;
}


::-webkit-scrollbar-track-piece {
    background-color: #f8f8f8;
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}

::-webkit-scrollbar-thumb {
    background-color: #dddddd;
    background-clip: padding-box;
    min-height: 28px;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #bbb;
}


.AppV3 {
    margin: 1vh 0;
    font-family: ubuntu, source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

div.card {
    background: white;
    min-width: 8vh;
    display: inline-flex;
    flex-direction: column;
    box-shadow: 6px 5px 0 1px rgba(70,70,70,0.25);
}

.card header {
    text-align: left;
    margin: 1.2em 0 2em;
    border-bottom: 1px solid black;
    font-size: 130%;
    font-weight: bold;
}

.card p {
    text-align: left;
}

div.ButtonGroup {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

div.LinkButton {
    display: inline-block;
    font-size: 120%;
    text-align: center;
    cursor: pointer;
    min-width: 2.8em;
    padding: 0.2em 0.8em;
    background: rgb(255,255,255) !important;
    color: rgb(64,196,99) !important;
    border: 1px solid rgb(64,196,99);
    transition: color 0.6s, background 0.6s;
    /* transition-duration: 0.6s; */
}

div.LinkButton:hover {
    background: rgb(64,196,99) !important;
    color: rgb(255,255,255) !important;
}

div.tip {
    padding: 0 0.25em;
    pointer-events: none;
    border: 1px solid rgb(204,179,143);
    background-color: white;
    color: rgb(255,127,127);
    font-weight: 600;
    font-size: 120%;
    border-radius: 0.4em;
    animation: twinkle 1.6s infinite alternate;
}

@keyframes twinkle {
    0% {
        opacity: 0.4;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes twinkle {
    0% {
        opacity: 0.4;
    }
    100% {
        opacity: 1;
    }
}


div.DisplayCardContainer {
    background-image: radial-gradient(circle farthest-side at 12% 4%, rgb(249,225,161), rgb(180,134,65) 18%, rgb(71,63,51) 50%, rgb(54,45,32));
}

div.card.DisplayCard {
    padding: 2vh 2vw !important;
    width: 70vw;
    max-width: 800px;
    line-height: 1.5em;

    background-color: rgba(24,31,40,0);
    background-image: url(/images/card-rect.png);
    background-size: 10px 10px;
}

div.DisplayCardContent {
    background-color: rgba(24,31,40,0.7);
    min-height: 60vh;
}

div.DisplayCardContent header {
    font-size: 125%;
    text-align: left;
    margin: 0.8em;
    border-bottom: none;
    font-weight: bold;
    letter-spacing: 1.2px;
    background-color: rgba(24,31,40,0.9);
}

div.DisplayCardContent header span {
    border-bottom: 1px solid white;
}

label.LangOption {
    color: rgb(246,246,246);
    transition: all 0.2s;
}

label.LangOption:hover {
    color: rgb(24,31,40);
    background-color: rgb(246,246,246);
}

label.LangOption svg path {
    stroke: rgb(246,246,246);
    transition: stroke 0.2s;
}

label.LangOption:hover svg path {
    stroke: rgb(24,31,40);
}

label.option {
    color: rgb(180,195,202);
    transition: color 0.2s;
}

label.option:hover {
    color: rgb(249,225,161);
}
